<script>
import { ref, watch } from 'vue'
import $ from 'jquery'
export default {
  props: ['pageActive'],
  setup (props, { emit }) {
    const page = props

    watch(page, (newVal) => {
      if (newVal.pageActive === 'about') {
        const letterTimer = ref(null)
        const letterDuration = 50
        const letterDelay = 50
        let letterIndex = -1
        const letterTotal = $('.about__title').find('.letter').length
        setTimeout(function runLetter () {
          if (letterIndex < (letterTotal - 1)) {
            letterIndex++
            $('.about__title').find('.letter').eq(letterIndex).addClass('up-action')
            letterTimer.value = setTimeout(runLetter, letterDuration)
          }
        }, letterDelay)
      }
    })

    function openPage (text, go) {
      emit('push-page', text, go)
      $('.about__title').find('.letter').removeClass('up-action')
    }
    const lang = ref('chi')
    function getLang (type) {
      lang.value = type
      setTimeout(() => {
        $('.about__content__info').removeClass('up-action')
      }, 0)
      setTimeout(() => {
        $('.about__content__info').addClass('up-action')
      }, 100)
    }

    return {
      openPage,
      page,
      lang,
      getLang
    }
  }
}
</script>
